@import "../../global";

.section-saran {
  display: flex;
  padding: 6vh 0;
  justify-content: space-between;
    .content {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      padding: 1rem;
      border-radius: $radius-button;
      margin: 0 auto;
      border: 2px solid $primary;
      width: 100%;
      box-shadow: 0 6px #3486463b;
      p {
        font-size: 18px;
        font-weight: 600;
      }
    }
}

@media only screen and (max-width: 500px){
  .section-saran {
    display: flex;
    padding: 6vh 0;
    justify-content: space-between;
    .container {
      display: flex;
      .content {
        grid-gap: 1rem;
        flex-direction: column;
        p {
          font-size: 16px;
          font-weight: 600;
        }
        .btn{
          width: 100%;
        }
      }
    }
  }
}